@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap);
body{margin:0;font-family:'Inter' ,'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.starter{font-family:'Inter';font-weight:700;color:#fff;height:100vh;font-size:18px;background-color:#ff4653}.section{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin-top:60px;margin-bottom:16px}.button__section{text-align:center}.avatar{border-radius:50%;width:100px;height:100px}.container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;max-width:600px;margin:0 auto}.description{text-align:center}.description__name{margin:0;margin-top:24px;font-weight:700;padding:0;color:#fff}.description__bio{font-size:16px;margin:0;margin-top:4px;padding:0;font-weight:500;color:#fff}

.button{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:10px;padding:5px;padding-right:20px;min-height:56px;border-radius:14px;background-color:#9b1b3b;text-align:center;font-family:'Inter';font-size:16px;color:#fff;font-weight:700;text-decoration:none;text-transform:capitalize;-webkit-transform:translateY(0);transform:translateY(0);transition:-webkit-transform 0.5s linear;transition:transform 0.5s linear;transition:transform 0.5s linear, -webkit-transform 0.5s linear}.button:hover{-webkit-transform:translateY(-10%);transform:translateY(-10%)}.button img{width:30px;height:100%;margin:0 20px}

